"use client";

import { useEffect, useState } from "react";
import { useParams, useRouter } from "next/navigation";
import { useAuth } from "../../../context/AuthContext";
import api from "../../../services/api";
import { FaArrowLeft, FaEdit, FaTrash, FaFilePdf } from "react-icons/fa";
import EmployeeModal from "@/components/EmployeeModal";

interface Employee {
  id: number;
  firstName: string;
  lastName: string;
  rate: number;
  workDays: number;
}

interface Project {
  id: number;
  name: string;
  location: string;
  description: string;
  offer: number;
  total: number;
}

interface Invoice {
  id: number;
  projectId: number;
  amount: number;
  status: string;
}

export default function EmployeeDetailsPage() {
  const { id } = useParams(); // get employee id from route
  const { token } = useAuth();

  const [employee, setEmployee] = useState<Employee | null>(null);
  const [projects, setProjects] = useState<Project[]>([]);
  const [invoices, setInvoices] = useState<Invoice[]>([]);
  const [loading, setLoading] = useState(true);

  const router = useRouter();
  
  useEffect(() => {
    if (!token || !id) return;

    const fetchData = async () => {
      try {
        // fetch employee
        const empRes = await api.get(`/api/employees/${id}`);
        setEmployee(empRes.data);

        // fetch projects for employee
        const projRes = await api.get(`/api/employees/${id}/projects`);
        setProjects(projRes.data);

        // fetch invoices for employee
        const invRes = await api.get(`/api/employees/${id}/invoices`);
        setInvoices(invRes.data);
      } catch (err) {
        console.error("Failed to fetch employee details", err);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [id, token]);

  if (loading) return <p className="text-center mt-5">Loading...</p>;
  if (!employee) return <p className="text-center mt-5">Employee not found</p>;

  return (
    <div className="container mt-4">
      {/* Employee Details */}
      <h2>
        {employee.firstName} {employee.lastName}
      </h2>
      <p>
        <strong>Rate:</strong> ${employee.rate}/day
      </p>
      <p>
        <strong>Work Days:</strong> {employee.workDays}
      </p>
      <hr />

      {/* Projects Section */}
      <h4>Projects</h4>
      {projects.length > 0 ? (
        <ul className="list-group mb-4">
          {projects.map((p) => (
            <li key={p.id} className="list-group-item">
              <strong>{p.name}</strong> – {p.location}  
              <br />
              Offer: ${p.offer}, Total: ${p.total}
            </li>
          ))}
        </ul>
      ) : (
        <p>No projects assigned.</p>
      )}

      {/* Invoices Section */}
      <h4>Invoices</h4>
      {invoices.length > 0 ? (
        <ul className="list-group">
          {invoices.map((i) => (
            <li key={i.id} className="list-group-item">
              Project #{i.projectId} – ${i.amount} ({i.status})
            </li>
          ))}
        </ul>
      ) : (
        <p>No invoices found.</p>
      )}
    </div>
  );
}
